/* BODY AND BACKGROUNDS */

.Store{
    background-image: url(../images/mainbg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 10em;
    display: flex;    
    flex-wrap: nowrap;
    height:100%;
}
.points{
    display: flex;
    align-self: flex-start;

}
.Avatar{
    flex: 'flex-shrink';
    padding-right: 8em;
    padding-top: 8em;
    margin-top: 5em;
    height: 2em;
    width: auto; 
}
.Acc{
    flex: 'flex-shrink';
    padding-top: 8em;
    padding-left: -10em;
}
.Store{
    position: absolute;
    width: 786px;
    height: 600px;
    left: 593px;
    top: 313px;
}
.Item{
    position: absolute;
    width: 200px;
    height: 189.09px;
    left: 623px;
    top: 414px;
    background: rgba(255, 249, 18, 0.78);
    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 8px;
}